const Services = () => {
  const { servicesCurrent } = useSelector((state: RootState) => state.sections);
  return (
    <section id="services" className={`page ${servicesCurrent === "current" ? "current" : "invisible"}`}>
      <div>
        services section
      </div>
    </section>
  )
}

export default Services;